@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.quoteContainer {
  display: flex;
  height: 50%;
  align-items: end;
  flex-direction: column;
  justify-content: space-between;
}

.quoteBox {
  color: rgb(22, 160, 133);
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  background-color: white;
  padding: 36px;
  height: fit-content;
  border-radius: 25px;
  font-size: 28px;
  animation: fadeIn 2s;
  -webkit-animation: fadeIn 2s;
  -moz-animation: fadeIn 2s;
  -o-animation: fadeIn 2s;
  -ms-animation: fadeIn 2s;
}

@media only screen and (max-width: 600px) {
  .quoteBox {
    padding: 16px;
    font-size: 20px;
  }
}

.button {
  border: none;
  font-size: 18px;
  text-align: center;
  color: rgb(22, 160, 133);
  background-color: white;
  padding: 12px;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 8px;
  border-color: white;
  border-width: 4px;
  border-style: solid;
}

@media only screen and (max-width: 600px) {
  .button {
    padding: 10px;
  }
}
button:active {
  background-color: rgb(22, 160, 133);
  border-color: white;
  color: white;
  border-width: 4px;
  border-style: solid;
}

.next {
  float: right;
}
.previous {
  float: left;
}

.author {
  font-size: 20px;
  margin-top: 12px;
}

@media only screen and (max-width: 600px) {
  .author {
    font-size: 16px;
  }
}

.quoteList {
  display: flex;
  align-items: start;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

.container {
  width: 546px;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 100%;
    margin-left: 28px;
    margin-right: 28px;
  }
}

.logos {
  border: none;
  position: absolute;
  top: 10px;
  left: 0;
  z-index: 1;
  width: 60px;
  text-align: center;
}

.logo {
  cursor: pointer;
  height: 50px;
  width: 50px;
  background-color: rgb(22, 160, 133);
  color: white;
  border-radius: 12px;
}

.share {
  padding-top: 20px;
  color: white;
  clear: both;
  font-size: 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.lang {
  display: flex;
  flex-direction: row;
}
.languageButton {
  font-size: 12px;
  margin-left: 8px;
}
